import React from "react";
import { Image, Notes, Slide } from "spectacle";

export default (
  <Slide
    transition={["fade"]}
    bgColor="secondary"
    bgImage="assets/chess.jpg"
    bgDarken="0.5"
  >

        <Image src="assets/chessboard.png" />
 

    <Notes></Notes>
  </Slide>
);



